import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { UserService } from '../user.service';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-loginnn',
  templateUrl: './loginnn.component.html',
  styleUrls: ['./loginnn.component.css']
})
export class LoginnnComponent implements OnInit {
  username: '';
  password: "";
  constructor(public userService: UserService, private http: HttpClient, private router: Router) { }

  ngOnInit() {
  }

  onLogin(form: NgForm) {
    if (form.invalid) {
      console.log('abc');
      return;
    }
    if ((form.value.username === 'admin') && (form.value.password === 'admin')) {
      this.router.navigateByUrl('admin');
    }
    else{
    console.log('form send ho gya');
    this.userService.login(form.value.username, form.value.password);
   // form.resetForm();
   
    }

  }

}
