import { Component, OnInit } from '@angular/core';
import { UserService } from '../user.service';
import { Subscription } from 'rxjs';
import { Admin } from '../admin.model';
import { NgForm } from '@angular/forms';
import { UserCar} from '../userCar.model';
import {formatDate} from '@angular/common';
@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {

  private date = formatDate(new Date(), 'yyyy-MM-dd', 'en');

  cars: Admin[] = [];
  private carsSub: Subscription;

  userCars: UserCar[] = [];
  private userCarsSub: Subscription;

  constructor(public userService: UserService) {
   
  }

  username = this.userService.user;

  load()
  {
    console.log("less");
  }
  
  ngOnInit() {

    
    this.userService.getCars();
    this.carsSub = this.userService.getAdminUpdateListener()
      .subscribe((cars: Admin[]) => {
        this.cars = cars;
      });

    this.userService.getUserCar();
    this.userCarsSub = this.userService.getuserCarsUpdateListener()
      .subscribe((userCars: UserCar[]) => {
        this.userCars = userCars;
        this.userCars = this.userCars.reverse();
      });

  }
}
