import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { UserCar} from './userCar.model';
import { User } from './user.model';
import { Admin } from './admin.model';
import { Router } from '@angular/router';
import {formatDate} from '@angular/common';

@Injectable({ providedIn: 'root' })
export class UserService {

  constructor(private http: HttpClient, private router: Router) {}
  private users: User[] = [];
  private bookings: UserCar[] = [];
  private records: Admin[] = [];
  private usersUpdated = new Subject<User[]>();
  private recordsUpdated = new Subject<Admin[]>();
  private userCarsUpdated = new Subject<UserCar[]>();
  public user: string ;
  public from;
  public to;

  setDate(from: Date,to:Date)
  {
    this.from = formatDate(from, 'yyyy-MM-dd', 'en');
    this.to = formatDate(to, 'yyyy-MM-dd', 'en');
  }
 
 login(username: string, password: string) {
  const login = { username, password};
  this.user = username;
  console.log(this.user);
  this.http.post<{ message: number}>(
           'http://localhost:3000/api/users', login).
           subscribe(responseData => {
            if (responseData.message == 1) {
              alert('Login successfull !!!');
              console.log('ho gya finally');
              this.user = username;
              this.router.navigateByUrl('account');
            }
            if(responseData.message != 1)
            {
              alert('username or password not correct');
            }

          });
  }

  addBooking(id: string) {
    const username = this.user;
    const dfrom = this.from;
    const dto = this.to;
    console.log(dfrom);
    console.log(id);
    const booking: UserCar = { id, username, dfrom, dto };
    this.http
    .post<{ message: string }>('http://localhost:3000/api/booking', booking)
    .subscribe(responseData => {
      console.log(responseData.message);
      this.bookings.push(booking);
      this.userCarsUpdated.next([...this.bookings]);
    });
  }

  getUserCar(){
    this.http
      .get<{ message: string; bookings: any }>(
        'http://localhost:3000/api/booking'
      )
      .pipe(map((userData) => {
        return userData.bookings.map(booking => {
          return {
            id: booking.id,
            username: booking.username,
            dfrom: booking.dfrom,
            dto: booking.dto
          };
        });
      }))
      .subscribe(transformedPosts => {
        this.bookings = transformedPosts;
        this.userCarsUpdated.next([...this.bookings]);
      });
  }

  getCars() {
    this.http
      .get<{ message: string; records: any }>(
        'http://localhost:3000/api/admins'
      )
      .pipe(map((userData) => {
        return userData.records.map(record => {
          return {
          _id:record._id,
          id :record.id,
          name: record.name,
          type: record.type,
          seater: record.seater,
          suv: record.suv,
          url: record.url,
          price: record.price,
          petrol: record.petrol,
          from: record.from,
          to: record.to
          };
        });
      }))
      .subscribe(transformedPosts => {
        this.records = transformedPosts;
        this.recordsUpdated.next([...this.records]);
      });
  }
  getPostUpdateListener() {
    return this.usersUpdated.asObservable();
  }

  getAdminUpdateListener() {
    return this.recordsUpdated.asObservable();
  }

  getuserCarsUpdateListener() {
    return this.userCarsUpdated.asObservable();
  }

  signup(username: string , email: string, password: string, mobile: string) {
    const user: User = { id: null, username, email, password, mobile};
    this.http
      .post<{ message: string }>('http://localhost:3000/api/new-users', user)
      .subscribe(responseData => {
        console.log(responseData.message);
        this.users.push(user);
        this.usersUpdated.next([...this.users]);
      });
  }

// tslint:disable-next-line: max-line-length
AddAdmin(id: string , name: string , suv: string, type: string , seater: string , url: string , price: string , petrol: string , from: string, to: string ) {
  const record: Admin = { id, name, suv, type, seater, url , price , petrol, from, to};
    // tslint:disable-next-line: align
    console.log('data app mai ja rha hai ');
  this.http
      .post<{ message: string }>('http://localhost:3000/api/admins', record)
      .subscribe(responseData => {
        console.log(responseData.message);
        console.log(record);
        this.records.push(record);
        this.recordsUpdated.next([...this.records]);
      });
  }
}


