import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs';
import { Admin } from '../admin.model';
import { UserService } from '../user.service';
import { CommonModule } from '@angular/common';
import { NgForm } from '@angular/forms';
import {formatDate} from '@angular/common';


@Component({
  selector: 'app-display',
  templateUrl: './display.component.html',
  styleUrls: ['./display.component.css']
})

export class DisplayComponent implements OnInit {
  date = formatDate(new Date(), 'yyyy-MM-dd', 'en');
  cars: Admin[] = [];
  private carsSub: Subscription;
  book = 'Book Ride';
  key = 'abc';
  value = 'abc';
  val1 = '';
  val2 = '';
  private s = 'false';
  constructor(public userService: UserService) { }

  ngOnInit() {
    this.userService.getCars();
    this.carsSub = this.userService.getAdminUpdateListener()
      .subscribe((cars: Admin[]) => {
        this.cars = cars;
      });
  }
  Book(id: string ) {
    if (this.s == 'true')    {
    alert('You Booked A Ride !!!!!!!!!!');
    this.userService.addBooking(id);
    } else {
      alert('kindly select journey details');
    }
  }

  search(form: NgForm) {
    if (form.value.dfrom < this.date) {
      alert('PickUp date cannot be in the past');
      // form.resetForm();
    }
    if (form.value.dfrom > form.value.dto) {
      alert('Drop date should be ahead than PickUp date');
      // form.resetForm();
    }
    else {
      this.userService.setDate(form.value.dfrom, form.value.dto);
      this.val1 = form.value.from;
      this.val2 = form.value.to;
      this.value = '';
      this.s = 'true';
    }
  }
  manual() {
     this.value = 'manual';
  }
  auto() {
    this.value = 'Automatic';
  }
  Hatch() {

    this.value = 'Hatchbatch';
  }
  sedan() {

    this.value = 'Sedan';
  }
  suv() {

    this.value = 'suv';
  }
  thar() {

    this.value = 'Jeep';
  }
  petrol() {

    this.value = 'Petrol';
  }
  diesel() {
    this.value = 'Diesel';
  }
  // tslint:disable-next-line: use-lifecycle-interface
  ngOnDestroy() {
    this.carsSub.unsubscribe();
  }

}
